import { Collapse} from "antd";
import { CInput, CSelect, CInputNumber } from "@components";

const CFixAssetGeneral= ()=>{
   const { Panel } = Collapse;
   const rowSpan = {
      span:{
         xs: 24,
         sm: 24,
         lg: 12,
      }
   };

   return (
      <>
         <Collapse defaultActiveKey={["0"]}>
            <Panel header="General" key="0">
               <fieldset>
                  <legend>IDENTIFICATION</legend>
                  <CSelect
                     label="Fix Asset Group"
                     name="fix_asset_group"
                     id="fix_asset_group"
                     isRequired
                     autoFocus
                  />

                  <CInputNumber
                     label="Number"
                     name="number"
                     id="number"
                     autoFocus
                  />
               </fieldset>
               
               <fieldset>
                  <legend>DESCRIPTION</legend>
                  <CInput
                     label="Name"
                     name="desc_name"
                     id="desc_name"
                     autoFocus
                  />

                  <CInput
                     label="Search Name"
                     name="search_name"
                     id="search_name"
                     autoFocus
                  />
               </fieldset>
               
               <fieldset>
                  <legend>TYPE</legend>
                  <CSelect
                     label="Type"
                     name="type"
                     id="type"
                     autoFocus
                  />

                  <CSelect
                     label="Major Type"
                     name="major_type"
                     id="major_type"
                     autoFocus
                  />

                  <CSelect
                     label="Property Type"
                     name="property_type"
                     id="property_type"
                     autoFocus
                  />
               </fieldset>

               <fieldset>
                  <legend>QUANTITY</legend>
                  <CInputNumber
                     label="Quantity"
                     name="quantity"
                     id="quantity"
                     autoFocus
                  />

                  <CSelect
                     label="Unit Of Measure"
                     name="unit_of_measure"
                     id="unit_of_measure"
                     autoFocus
                  />

                  <CInputNumber
                     label="Unit Cost"
                     name="unit_cost"
                     id="unit_cost"
                     autoFocus
                  />
               </fieldset>

               <fieldset>
                  <legend>ENTRY TYPE</legend>
                  <CSelect
                     label="Asset Activity Code"
                     name="asset_activity_code"
                     id="asset_activity_code"
                     autoFocus
                  />

                  <CSelect
                     label="Property Group"
                     name="property_group"
                     id="property_group"
                     autoFocus
                  />
               </fieldset>
               
            </Panel>
         </Collapse>
      </>
   )
}
export default CFixAssetGeneral;